// This file contains all application-wide Sass variables.

// Colors
$primary-color: #F9A441;
$secondary-color: #2E2427;
$tertiary-color: #6E1141;
$bg-dark-gray: #42474C;

.color-tertiary {
    color: $tertiary-color !important;
}

// Layout
$section-spacing: 0 0.66667em;
$layout-restrained: 0 calc(50% - 425px);

// body copy
$text-font-stack: 'Cabin', sans-serif;

// Copy text color
// @type Color
$text-color: rgb(34, 34, 34) !default;

// Main brand color
// @type Color
$brand-color: #9DA890;

// Light grey
// @type Color
$light-grey: rgb(237, 237, 237) !default;

// Medium grey
// @type Color
$mid-grey: rgb(153, 153, 153) !default;

// Dark grey
// @type Color
$dark-grey: rgb(68, 68, 68) !default;

